import { destroyer, styler, updater } from './base';
import type { ExioNode } from './base';
import { exioComponent } from './component';

export function createExioFont(): void {
  let font = document.querySelector('#exio-font') as HTMLStyleElement | null;
  if (!font) {
    font = document.createElement('style');
    font.id = 'exio-font';
    font.innerHTML = `
      @font-face {
        font-family: 'Exio Icons';
        font-style: normal;
        font-weight: 400;
        font-display: swap;
        /* https://fonts.gstatic.com/s/materialicons/v126/flUhRq6tzZclQEJ-Vdg-IuiaDsNc.woff2 */
        src: url('data:@file/octet-stream;base64,') format('woff2');
      }
    `;
    document.body.appendChild(font);
  }
}

export const iconVars = {};

export type ExioIconArgs = Partial<{
  [Prop in keyof typeof iconVars]: typeof iconVars[Prop]['val'] | string;
}>;

export const exioIcon = (
  node: HTMLElement,
  opts: ExioIconArgs = {}
): ExioNode<ExioIconArgs> => {
  const component = exioComponent(node);
  createExioFont();
  const s = styler(node);
  s.innerHTML = `
    .${s.id} {
      font-family: 'Exio Icons';
    }
  `;
  return {
    ...updater(opts, node, iconVars),
    ...destroyer(() => {
      s.remove();
      component.destroy();
    }),
  };
};
